.App {
	text-align: center;
	box-sizing: border-box;
	padding: 0 1em 1em;
}

h1,
h2 {
	margin: 0;
	padding: 3vh 0;
}

p {
	margin: 0;
}

.page-body {
	background-color: #fff;
	max-width: 100%;
	box-shadow: 0 1px 3px #777;
	border-radius: 8px;
	overflow: auto;
}

@media only screen and (min-width: 768px) {
	.App {
		padding-left: 15vw;
		padding-right: 15vw;
		padding-bottom: 10vh;
	}
	.page-body {
		box-shadow: 5vmin 5vmin #111a;
		padding: 0 10vmin;
	}
}
