.home-img-container {
	background: url('./assets/home.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 80vmin;
	color: #fff;
	margin-bottom: 10vmin;
}

.home-text-container {
	text-align: justify;
	font-size: larger;
	padding: 0 1em 1em;
}

@media only screen and (min-width: 768px) {
	.home-text-container {
		padding: 0 5vmin 1em;
	}
}
