.thumbnail-container > img {
	width: 100%;
	border-radius: .5em;
	transition: .3s;
	box-shadow: 0 1px 2px #0004;
}

.thumbnail-container > img:hover {
	transform: scale(0.9);
}

.thumbnail-container {
	margin-bottom: 2em;
	flex: 0 0 30%;
}

.Modal {
	overflow: auto;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #fff;
	padding: 5vmin;
	text-align: center;
	-webkit-overflow-scrolling: touch;
}

.Modal:focus {
	outline: none;
}

.close-button {
	background-color: #fc0;
	position: fixed;
	bottom: 1.5em;
	right: 1em;
	height: 3.5em;
	line-height: 3.5em;
	border-radius: 50%;
	width: 3.5em;
	box-shadow: 0 1px 2px #000a;
	cursor: pointer;
}

.Modal > a {
	font-size: large;
	color: cadetblue;
}

.Modal > p {
	text-align: justify;
	font-size: large;
	margin-bottom: 1em;
	padding: 0 1em;
}

.Overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #0005;
}

.ReactModalPortal > * {
	opacity: 0;
}
.ReactModal__Overlay {
	transition: opacity 300ms ease-in-out;
	background: #0005;
}

.ReactModal__Overlay--after-open {
	opacity: 1;
}

.ReactModal__Overlay--before-close {
	opacity: 0;
}

.app-img-container {
	margin-bottom: 1em;
}

.app-img-container > img {
	width: 100%;
}

@media only screen and (min-width: 768px) {
	.thumbnail-container > img {
		cursor: pointer;
	}

	.Modal {
		top: 5vh;
		left: 10vw;
		right: 10vw;
		bottom: 5vh;
		border-radius: 8px;
	}

	.close-button {
		bottom: auto;
		top: 2.5vh;
		right: 8vw;
	}

	.Modal > p {
		padding: 0 10vw;
		margin-bottom: 2em;
	}

	.app-imgs-container {
		display: flex;
		justify-content: space-around;
		align-items: center;
	}

	.app-img-container {
		width: 30%;
	}

	.app-img-container > img {
		width: 100%;
	}

	.app-imgs-container-col > .app-img-container {
		width: 100%;
	}
}
